@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
.container-projects {
    padding: 0 30px 0;
    display: flex;
    flex-direction: column;
}

#title-projects {
    text-align: center;
    font-weight: bold;
    font-size: 30px;
    margin-bottom: 5px;
}


.list-projects {
    display: grid;
    width: 100%;
    padding: 30px;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 15px;
    row-gap: 15px;
    grid-column-gap: 30px;
    -webkit-column-gap: 30px;
            column-gap: 30px;
    list-style: none;
}

.list-projects li {
    padding: 10px 10px 10px 30px;
    text-align: justify;
    border-radius: 8px;
    border: 1px solid #b4b4b4;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.4);
}

.list-projects li p span {
    font-weight: bold;
}
.list-projects li #title span {
    margin-left: 10px;
}


.list-projects li #title {
    font-weight: 700;
    font-size: 20px;
    text-align: center;
}

@media (max-width: 720px){
    .list-projects {
        grid-template-columns: repeat(1, 1fr);
    }
    
    .list-projects li {
        padding: 5px 5px 5px 15px;
    }
}

@media (max-width: 400px){
    .container-projects{
        padding: 10px;
    }

    .list-projects{
        font-size: 14px;
    }
    
    .list-projects li{
        padding: 2px 2px 2px 7.5px;
    }
}

:root{
    --main-border-colour: #701caa;
    --link-colour: #501B65;
}

* {
    box-sizing: border-box;
}

html{
    height: 100%;
    max-height: 100vh;
}

body{
    background-color: #701caa;
    background-color: var(--main-border-colour);
    font-family: Roboto, sans-serif;
}

header {
    max-height: 250px;
    min-height: 70px;
    position: fixed;
    left: 0;
    top: 0;
    background-color: #701caa;
    background-color: var(--main-border-colour);
    background-color: #fff;
    width: 100%;
    box-shadow: 1px 0px 3px #4b4b4b;
}

.spacer {
    margin-top: 80px;
}

.main-nav{
    max-height: 65px;
    min-height: 65px;
    margin-right: 0;
    padding: 10px 20px;
    height: 8vh;
    font-weight: bold;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
}

nav *{
    border-radius: 4px;
}

button:focus{
    outline: 0;
}

.mobile-bar {
    display: none;
}

.mobile-bar button{
    display: inline-block;
    background-color: #FFF;
    border-radius: 10px;
    width: 20px;
    height: 20px;
    border: 0;
    padding: 2px;
}   

.nav-social-links li a {
    color: #501B65;
    color: var(--link-colour);
}

.nav-social-links li {
    margin: 15px;
    display: inline-block;
    background-color: #fff;
    border: 1px solid #501B65;
    border: 1px solid var(--link-colour);
    padding: 6px;
}

.home-button {
    width: 100%;
    margin: 0;
    max-width: 300px;
    display: flex;
    justify-content: space-around;
}

.home-button span{
    color: #501B65;
    color: var(--link-colour);
    margin-left: 8px;
}

.home-button-link:hover {
    opacity: 0.7;
}

a {
    color: #000;
    text-decoration: none;
    transition: opacity 0.25s;
}

.nav-social-links a:hover{
    opacity: 0.7;
}

.nav-social-links a span{
    margin-left: 8px;
}

@media (min-width: 806px){
    .home-button-link {
        padding-top: 12px;
    }
    
    header {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    .nav-social-links {
        display: block;
        width: 100%;
    }

    .nav-social-links ul {
        float: right;
        position: relative;
        right: 0;
        margin: 0;
        margin-top: 6px;
        margin-right: 15px;
        padding-left: 0px;
    }

    .nav-social-links ul li{
        margin-left: 2px;
    }

    .main-nav {
        flex-direction: row;
        justify-content: flex-start;
        width: 100%;
    }
}

@media (max-width: 805px){
    .mobile-bar{
        display: block;
        padding-top: 4px;
    }

    .home-button{
        justify-self: flex-end;
        width: 100%;
        margin: 0 auto;
        text-align: center;
    }

    nav {
        width: 100%;
        min-height: auto;
        max-height: 200px;
        height: 100%;
    }

    .nav-social-links{
        float: none;
        margin: 0;
        display: none;
        width: 100%;
        -webkit-padding-start: 0px;
                padding-inline-start: 0px;
    }

    .nav-social-links ul {
        margin: 0;
        padding: 10px;
        width: 100%;
        max-width: 100%;
    }

    .nav-social-links li {
        text-align: center;
        display: block;
    }

    .nav-social-links li a{
        display: block;
        padding: 0;
        width: 100%;
    }
}
:root{
    --main-border-colour: #701caa;
}

*{
    box-sizing: border-box;
}

main{
    margin: 0 auto;
    border-radius: 8px;
    background-color:  #701caa;
    background-color:  var(--main-border-colour);
    padding: 4px;
    max-width: 80vw;
    height: 100%;
    margin-top: 40px;
}

article{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 0px;
    margin-bottom: 0px;
    border-radius: 8px;
    background-color: #FFF;
    width: 100%;
    height: 100%;
    font-family: Roboto;
    box-shadow: 2px 2px 4px #383333;
}

#container-text #text, #container-text #title{
    padding: 15px 15px 15px 60px;
    width: 100%;
    max-width: 600px;
}

#title{
    font-weight: bold;
    font-size: 30px;
}

#text{
    margin-top: 40px;
    font-weight: 500;
    font-size: 22px;
    text-align: justify;
}

#container-img{
    box-sizing: border-box;
    padding: 40px 40px;
}

#container-img img {
    display: inline-block;
    margin: 2vh 0;
    height: 100%;
    width: 100%;
    max-width: 400px;
    max-height: 500px;
}

@media (max-width: 900px){
    article {
        flex-direction: column;
    }
    
    #container-text #text{
        padding-left: 20px;
        margin-top: 20px;
        font-size: 18px;
    }

    #container-text #title{
        margin-bottom: 20px;
        padding-left: 20px;
    }

    #container-img img{
        margin: 0;
    }
}



